.profileInfo{
    background-color: #1a1a1a;
    padding: 60px 0;
    position: relative;
  }

  .profileInfo::before{
    content: '';
    background: url("./../../../assets/shapes-bg.png");
    background-size: contain;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.1;
    width: 100%;
    height: 100%;
    z-index: -10000;
  }
  
  .photoWrapper img{
    max-width: 180px;
    height: auto;
    border-radius: 10px;
    border: 3px solid #fff;
  }

  .Info{
    display: flex;
    justify-content: center;
  }
  
  .Info div{
    margin: 0 15px;
  }

  .userMini{
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }

  .contacts{
    margin-bottom: 20px;
  }

  .contacts a{
    color: #fff;
    padding: 10px;
    background-color: #fa2276;
    border-radius: 50%;
    margin: 20px 10px;
  }


