
  .userMini{
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }

  .commentContainer{
    background-color: #1a1a1a;
    padding: 15px;
    margin-top: 20px;
  }

  .commentRow{
    display: flex;
    align-items: center;
  }

  .postForm textarea{
    width: 83%;
    height: 40px;
    float: left;
    background-color: transparent;
    font-size: 12px;
    padding: 5px;
  }

  .commentPost {
    padding: 10px 30px;
    background: #fa2276;
    border: 1px solid #fa2276;
    border-radius: 3px;
    min-height: 40px;
    font-size: 14px;
    color: #fff;
    cursor: pointer;
    font-weight: 400;
    float: right;
    width: 13%;
}

textarea{
  color: #fff;
  padding: 15px;
  outline: none;
}


